<template>
	<view>
		<!-- 页面顶部信息  -->
		<view>
			<view class="margin-top-sm" v-for="(item,index) in noticeList" :key="index"> 
			
				<navigator :url="'/pages/home/noticedetail?id='+item.id" hover-class='none'>
					<!-- 创建时间 -->
					<view class="flex flex-wrap text-blod justify-center">
						<view class="basis-df text-center bg-gray">
							 {{item.createTime}}
						</view>
					</view>
					
					<!-- 卡片 -->
					<view class="cu-card article">
						<view class="cu-item shadow-blur">
							<!-- 卡片标题 -->
							<view class="title text-cut solid-bottom">
								 {{item.title}}
							</view>
							<!-- 卡片简介 -->
							<view class="content solid-bottom padding-sm padding" >
								 {{item.description + '...'}}  
							</view>
							<!-- 卡片底部 -->
							<view class="foot">
								<view class="foot padding-sm margin-top-sm">
									<view class="flex  justify-between">
										<view class="margin-xs radius">查看详情</view>
										<view class="margin-xs radius"><text class="lg cuIcon-right text-sl" ></text></view>
									</view>
								</view>
							</view>
						</view>
					</view>
				</navigator>
				
				
			</view>
		</view>
	</view>
</template>

<script>
	import notice from '../../api/notice.js'
	export default{
		data(){
			return{
				// 通知与公告列表
				noticeList:[]
			}
		},
		// 监听页面加载
		onLoad(){
			this.initNoticeList();
		},
		// 监听页面下拉刷新
		onPullDownRefresh() {
			console.log('refresh');
			setTimeout(function() {
				//uni.stopPullDownRefresh();
			}, 1000);
		},
		methods:{
			 // 初始化通知列表
			 async initNoticeList(){
				const [err,res] = await notice.noticeList();
				if(res.data.code === 200){
					this.noticeList = res.data.data;
				}
				console.log(res);
			}, 
			removeTAG(html){
				return this.$tagutil.removeTAG(html)
			}
		}
	}
</script>

